import Vue from 'vue'
import Main from './main.vue'

const Constructor = Vue.extend(Main)

const requestAll = arr => Promise.all(arr)

const $loading = (options = {}) => new Promise(async (resolve) => {
  const instance = new Constructor({
    data: options
  })

  const { requestList = [] } = options

  instance.$mount()

  document.body.appendChild(instance.$el)

  instance.open()

  await requestAll(requestList)

  instance.close()

  resolve()
})

export { $loading }
