import qs from 'querystringify'

const isDev = process.env.NODE_ENV === 'development'

const search = !isDev ? window.location.search : `?isForce=0&stuId=12491&stuCouId=9642934&packageId=41312&liveId=340493&packageSource=2&packageAttr=1&releasedPageInfos=[%7B%2242689%22:[%2222%22,%2220276%22]%7D,%7B%2242688%22:[%2222%22,%22100922%22]%7D]&isPlayBack=0&classId=30951&classTestId=0&srcTypes=22,22&testIds=20276,100922&educationStage=3&teamId=1&nonce=12491_1547464034314&entranceTime=1547464034`

// ?stuId=99916&liveId=255783&stuCouId=17558382&classId=99772&teamId=1&packageId=38593&packageSource=2&packageAttr=1&classTestId=0&releasedPageInfos=[{"42151":["19","489440"]}]&isPlayBack=0&educationStage=3&isShowTeamPk=0&nonce=99916_1550460486038

// ?stuId=12491&liveId=340493&stuCouId=9642934&classId=30951&teamId=1&packageId=41312&packageSource=2&packageAttr=1&classTestId=0&releasedPageInfos=[{%2242689%22:[%2222%22,%2220276%22]},{%2242688%22:[%2222%22,%22100922%22]}]&isPlayBack=0&educationStage=3&isShowTeamPk=0&nonce=12491_1547464034314

console.log('当前 url query 参数:', qs.parse(search))

const {
  forceSubmit,
  stuId,
  stuCouId,
  packageId,
  liveId,
  packageSource,
  packageAttr,
  releasedPageInfos,
  isPlayBack,
  classId,
  classTestId,
  educationStage,
  teamId,
  nonce,
  chs
} = qs.parse(search)

const releasedPageInfosObject = JSON.parse(releasedPageInfos)

const srcTypesData = []
const testIdsData= []

for (let i=0; i<releasedPageInfosObject.length; i++) {
  const item = releasedPageInfosObject[i]
  for (let j in item) {
    srcTypesData.push(item[j][0])
    testIdsData.push(item[j][1])
  }
}

const srcTypes = srcTypesData.join(',')
const testIds = testIdsData.join(',')

const queryData = {
  forceSubmit,
  stuId,
  stuCouId,
  packageId,
  liveId ,
  packageSource,
  packageAttr,
  releasedPageInfos,
  isPlayBack,
  classId,
  classTestId,
  srcTypes,
  testIds,
  educationStage,
  teamId,
  nonce,
  chs
}

console.log('解析完成的 query 参数:', queryData)

export default queryData
