const base = {
  wrong: {
    title: '全部错误',
    textAlign: 'right',
    line: {
      start: [7.76, 0.7],
      end: [9.92, 0.7]
    }
  },
  partRight: {
    title: '部分正确',
    textAlign: 'right',
    line: {
      start: [7.76, 2],
      end: [9.92, 2]
    }
  },
  right: {
    title: '全部正确',
    textAlign: 'left',
    line: {
      start: [3.76, 1.4],
      end: [1.6, 1.4]
    }
  }
}

const colorMap = {
  right: '#28B3AC',
  partRight: '#FFBB67',
  wrong: '#FF7878'
}

const initChartInfo = (ctx, color, preset, type, scale) => {
  const r = 0.12 * scale
  const x = base[type].line.start[0] * scale
  const y = base[type].line.start[1] * scale

  ctx.save()
  ctx.fillStyle = color
  ctx.arc(x, y, r, 0, 2 * Math.PI)
  ctx.fill()
  ctx.restore()

  const endx = base[type].line.end[0] * scale
  const endy = base[type].line.end[1] * scale
  const text = base[type].title

  let presetNum = '0%'

  if (preset * 1 === 1) {
    presetNum = '100%'
  } else if (!(preset * 1)) {
    presetNum = '0%'
  } else {
    presetNum = Math.round(preset * 100) + '%'
  }

  ctx.save()
  ctx.strokeStyle = color
  ctx.fillStyle = color
  ctx.lineWidth = 0.04 * scale
  ctx.textAlign = base[type].textAlign
  ctx.font = 0.3 * scale + 'px Arial'
  ctx.beginPath()
  ctx.lineTo(x, y)
  ctx.lineTo(endx, endy)
  ctx.stroke()
  ctx.fillText(presetNum, endx, endy - 0.13 * scale)
  ctx.fillText(text, endx, endy + 0.35 * scale)
  ctx.restore()
}

export default function pie (el, data) {
  const canvas = el.children[0]
  const ctx = canvas.getContext('2d')
  const scale = (document.documentElement.clientWidth / 1334) * 100 * 2
  const W = canvas.width = 11.94 * scale
  const H = canvas.height = 3 * scale
  const centerX = W / 2
  const centerY = H / 2
  const from = 0.6 * scale
  const to = 1.38 * scale

  let start = 3 * Math.PI / 2
  let end = start
  let color = '#fff'

  ctx.clearRect(0, 0, W, H)

  for (let i=0; i<data.length; i++) {
    const preset = Math.PI * 2 * data[i].preset

    end = start + preset

    color = colorMap[data[i].type]

    ctx.save()
    ctx.fillStyle = color
    ctx.beginPath()
    ctx.arc(centerX, centerY, to, end, start, true)
    ctx.arc(centerX, centerY, from, start, end)
    ctx.closePath()
    ctx.fill()
    ctx.restore()

    start = end

    initChartInfo(ctx, color, data[i].preset, data[i].type, scale)
  }
}
