const addTransition = (el) => {
  el.style.transition = 'transform 0.15s linear'
  el.style.webkitTransition = 'transform 0.15s linear'
}

const removeTransition = (el) => {
  el.style.transition = 'none'
  el.style.webkitTransition = 'none'
}

const setTranslate = (el, x) => {
  el.style.transform = `translate3d(${x}, 0, 0)`
  el.style.webkitTransform = `translate3d(${x}, 0, 0)`
}

export default function swiper (el, distance, cb) {
  const moveGroup = el.children[0]
  const moveLength = moveGroup.children.length

  let startX = 0
  let startY = 0
  let index = 0
  let isMoved = false
  let dx = 0
  let dy = 0
  let diff = 0
  let startTime = 0

  setTranslate(moveGroup, -index * distance / 100 + 'rem')

  el.addEventListener('touchstart', (e) => {
    startX = e.touches[0].pageX
    startY = e.touches[0].pageY
    startTime = Date.now()
  })

  el.addEventListener('touchmove', (e) => {
    const { pageX, pageY } = e.touches[0]

    dx = pageX - startX
    dy = pageY - startY

    diff = Math.abs(dx) - Math.abs(dy)

    if (diff < 10) {
      return false
    } else {
      e.preventDefault()
    }

    removeTransition(moveGroup)

    let d = -index * distance + dx

    if (dx > 0 && index === 0) d = 0
    if (dx < 0 && index === moveLength - 1) d = (moveLength - 1) * -distance

    setTranslate(moveGroup, (d) / 100 + 'rem')

    isMoved = true
  })

  el.addEventListener('touchend', () => {
    if (isMoved && Math.abs(dx) > distance / 6 && Date.now() - startTime > 50) {
      if (dx > 0) index--
      if (dx < 0) index++
      index = Math.max(0, index)
      index = Math.min(index, moveLength - 1)
    }

    addTransition(moveGroup)
    setTranslate(moveGroup, -index * distance / 100 + 'rem')
    cb && cb(index)

    startX = startY = dx = dy = 0
    isMoved = false
  })
}
