<template>
  <div class="loading" v-show="visible">
    <img class="img" src="../../assets/loading.gif" >
    <div class="bar">
      <div class="percent"></div>
    </div>
    <div class="meta">{{ message }}</div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      visible: false,
      message: '加载中，请稍后...'
    }
  },
  methods: {
    open () {
      this.visible = true
    },
    close () {
      this.visible = true
      this.$el.parentNode.removeChild(this.$el)
      this.$destroy(true)
    }
  }
}
</script>

<style lang="scss" scoped>
  .loading {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 2000;
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    .img {
      width: 2.2rem;
      height: 1.85rem;
      margin-bottom: 0.3rem;
      display: block;
    }

    .bar {
      width: 7rem;
      height: 0.1rem;
      background-color: #000;
      border-radius: 50rem;
      position: relative;
      overflow: hidden;
      margin-bottom: 0.3rem;

      .percent {
        width: 36%;
        height: 100%;
        position: absolute;
        left: -10%;
        bottom: 0;
        border-radius: 50rem;
        background-color: #F13232;
        animation: loadingAnimation 2s infinite linear;
      }
    }

    .meta {
      font-size: 0.32rem;
      color: #fff;
    }
  }

  @keyframes loadingAnimation {
    0% {
      left: -10%;
    }

    50% {
      left: 74%;
    }

    100% {
      left: -10%;
    }
  }
</style>

