<template>
  <div :class="['result-root', getter_resultBackgroundClass]">
    <!-- 结果数据 -->
    <div class="result-info">{{ getter_resultPageText }}</div>

    <!-- 关闭按钮 -->
    <div class="close" @click="handleCloseWebPage"></div>

    <!-- 滑动区域 -->
    <div class="swiper" ref="swiperRef">
      <div class="swiper-list-group">

        <!-- 答题情况 -->
        <div class="swiper-item">
          <div class="answer-info">
            <div class="header">
              <div class="i index">题号</div>
              <div class="i expect">正确答案</div>
              <div class="i yours">你的答案</div>
              <div class="i result">答案情况</div>
            </div>
            <div class="content">

              <div class="answer-item" v-for="(data, index) in getter_studentAnswerList" :key="data.testId">
                <div class="i index">{{ index + 1 }}</div>
                <div class="i expect">
                  <div class="answer-wrap">
                    <div v-for="item in data.rightAnswer" :key="item">{{ item }}</div>
                  </div>
                </div>
                <div class="i yours">
                  <div class="answer-wrap">
                    <div
                      v-for="(item, i) in data.stuAnswer"
                      :key="i"
                      :class="{ r: item.right*1 === 1, w: item.right*1 === 0, sf: !item.answer }"
                    >
                      {{ item.answer ? item.answer : '未作答' }}
                    </div>
                  </div>
                </div>
                <div
                  class="i result"
                  :class="{ r: data.isRight*1 === 2, p: data.isRight*1 === 1, w: data.isRight*1 === 0 }"
                />
              </div>

            </div>
          </div>
        </div>

        <!-- 图表统计 -->
        <div class="swiper-item">
          <div class="pie-info">
            <div class="header">
              <span class="icon" :class="getter_resultBackgroundClass"></span>
              组内答题情况
            </div>
            <div class="pie" ref="chartsRef">
              <canvas id="canvas"></canvas>
            </div>
          </div>
        </div>
        <!-- 排行榜 -->
        <div class="swiper-item" style="background-color: rgba(0, 0, 0, 0);">
          <div class="top-info">
            <div class="top-15 left" :class="getter_resultBackgroundClass">
              <div class="header">组内前15名</div>
              <div class="list">
                <div class="list-header">
                  <div class="index">排名</div>
                  <div class="name">姓名</div>
                  <div class="status">正确率</div>
                </div>
                <div class="list-content">
                  <div class="item" v-for="(data, index) in getter_teamGroupTop15Data" :key="data.stuId">
                    <div
                      class="index"
                      :class="{ one: index === 0, two: index === 1, three: index ===2 }"
                    >
                    {{ index > 2 ? index + 1 : '' }}
                    </div>
                    <div class="name" :class="{ own: data.isMe*1 === 1 }">{{ data.stuName }}</div>
                    <div class="status" :class="{ own: data.isMe*1 === 1 }">{{ data.correctRate }}</div>
                  </div>
                </div>
              </div>
            </div>
            <div class="top-15 right" :class="getter_resultBackgroundClass">
              <div class="header">班内前15名</div>
              <div class="list">
                <div class="list-header">
                  <div class="index">排名</div>
                  <div class="name">姓名</div>
                  <div class="status">正确率</div>
                </div>
                <div class="list-content">
                  <div class="item" v-for="(data, index) in getter_teamClassTop15Data" :key="data.stuId">
                    <div
                      class="index"
                      :class="{ one: index === 0, two: index === 1, three: index ===2 }"
                    >
                    {{ index > 2 ? index + 1 : '' }}
                    </div>
                    <div class="name" :class="{ own: data.isMe*1 === 1 }">{{ data.stuName }}</div>
                    <div class="status" :class="{ own: data.isMe*1 === 1 }">{{ data.correctRate }}</div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

      </div>
    </div>

    <div class="swiper-pointer-group">
      <span
        class="pointer"
        :class="{ active: var_currentPointIndex === i - 1 }"
        v-for="i in 3"
        :key="i"
      />
    </div>
  </div>
</template>

<script>
import queryParams from 'query-params'
import createApi from 'create-api'
import swiper from './utils/swiper.js'
import createPie from './utils/createPie.js'
import { $loading } from './components/loading'
import { $createRightAnimation } from './components/rightAnimation'

const postToAPP = (data) => {
  if (window.webkit && window.webkit.messageHandlers.xesApp) {
    window.webkit.messageHandlers.xesApp.postMessage(JSON.stringify(data))
  } else if (window.xesApp) {
    window.xesApp[data.methodName](JSON.stringify(data))
  }
}

const isForceSubmit = !!(queryParams.forceSubmit*1)
const isPlayBack = !!(queryParams.isPlayBack*1)
const isChs = !!(queryParams.chs*1)

const sleep = d => new Promise(resolve => setTimeout(resolve, d))

export default {
  data () {
    return {
      var_initReady: false,
      var_isForceSubmit: isForceSubmit,
      var_isPlayBack: isPlayBack,
      var_teamAnsweredPieData: null,
      var_teamTop15ListData: {},
      var_studentAnsweredResultData: {},
      var_chartsData: null,
      var_currentPointIndex: 0,
      var_requestTeamPieDataTimes: 0,
      var_requestTeamTop15Times: 0,
      var_maxRequestTimes: 15,
      var_teacherHasTakeUp: false,
      var_isChs: isChs
    }
  },

  computed: {
    getter_isShowResultPage () {
      return this.var_studentAnsweredResultData && this.var_teamAnsweredPieData
    },

    getter_resutlPageTypeData () {
      const { gold, type } = this.var_studentAnsweredResultData

      let backgroundClass = '', resultText = ''

      switch (type) {
        case 0:
          backgroundClass = 'w'
          resultText = '很遗憾答错了，没有获得金币哟！'
          break;
        case 1:
          backgroundClass = 'r'
          resultText = `恭喜你答对了，获得${gold}个金币！`
          break;
        case 2:
          backgroundClass = 'p'
          resultText = `部分正确，获得${gold}个金币！`
          break;
      }

      return { backgroundClass, resultText }
    },

    getter_resultBackgroundClass () {
      return this.getter_resutlPageTypeData.backgroundClass
    },

    getter_resultPageText () {
      return this.getter_resutlPageTypeData.resultText
    },

    getter_studentAnswerList () {
      return this.var_studentAnsweredResultData.answerLists || []
    },

    getter_teamGroupTop15Data () {
      return this.var_teamTop15ListData ? this.var_teamTop15ListData.groupRank : []
    },

    getter_teamClassTop15Data () {
      return this.var_teamTop15ListData ? this.var_teamTop15ListData.classRank : []
    }
  },

  created () {
    window._fn_({ fontSize: 100, width: 1334 })
  },

  async mounted () {
    window.__CLIENT_SUBMIT__ = this.handleTeacherTackUp.bind(this)
    if (window.location.hash === '#hasTakeUp') {
      this.var_teacherHasTakeUp = true
    }
    await this.initResultPage()
    postToAPP({ methodName: 'resultPageLoaded' })
  },

  methods: {
    async initResultPage () {
      await this.initSwiper()

      await $loading({
        requestList: [
          this.requestAllResultPageData()
        ]
      })

      // if (!this.var_isChs) {
        await this.requestStudentRightManyTimeData()
      // }

      this.var_initReady = true

      setInterval(() => {
        if (++this.var_requestTeamPieDataTimes < this.var_maxRequestTimes) {
          this.requestTeamAnsweredPieData()
        }
        if (this.var_isForceSubmit || this.var_teacherHasTakeUp) {
          if (++this.var_requestTeamTop15Times < this.var_maxRequestTimes) {
            this.requestStudentAnwseredResultData()
          }
        }
      }, 8000)
    },

    // 获取激励连对数量接口
    async requestStudentRightManyTimeData () {
      const ifshow = await createApi.API_ifNeedShowRightAnimation(queryParams)

      const isOpen = ifshow.data.isOpen

      if (!isOpen) return console.log('未开启连对')

      const res = await createApi.API_getStudentRightManyTimeData(queryParams)
      // const res = { data: {num: 29} }

      console.log('学生作答连对数量 ===>', res.data)

      if (res.data && res.data.num) {
        let num = res.data.num

        let jsonIndex = Math.min(8, num%2 ? num - 1 : num)

        num = Math.min(25, num)

        const animationPath = `animations/${jsonIndex}/data-${num}.json`

        await $createRightAnimation({ animationPath })
      }
    },

    requestAllResultPageData () {
      const isRequestTeamTop15 = this.var_isForceSubmit || this.var_teacherHasTakeUp || this.var_isPlayBack

      setTimeout(() => {
        isRequestTeamTop15 ? this.requestTeamTop15ListData() : Promise.resolve()
      }, 0)

      return Promise.all([
        this.requestStudentAnwseredResultData(),
        this.requestTeamAnsweredPieData(),
        isRequestTeamTop15 ? this.requestTeamTop15ListData() : Promise.resolve()
      ])
    },

    // 获取学生作答情况，自身答案和正确答案统计列表
    async requestStudentAnwseredResultData () {
      const res = await createApi.API_getStudentAnsweredResultData(queryParams)

      console.log('学生作答结果和答案数据 ===>', res.data)

      this.var_studentAnsweredResultData = res.data
    },

    // 获取组内作答的饼图统计接口
    async requestTeamAnsweredPieData () {
      const res = await createApi.API_getTeamAnwseredResultPieData(queryParams)

      console.log('组内答题情况饼图数据 ===>', res.data)

      this.initChartsForPie(res.data)
    },

    async requestTeamTop15ListData () {
      const res = await createApi.API_getTeamTop15ListData(queryParams)

      console.log('战队排名数据 ===>', res.data)

      this.var_teamTop15ListData = res.data
    },

    initSwiper () {
      return new Promise((resolve) => {
        setTimeout(() => {
          swiper(this.$refs.swiperRef, 1214,  index => this.var_currentPointIndex = index)
          resolve()
        }, 30)
      })
    },

    initChartsForPie (data) {
      const { totalNum = 1, charts = [] } = data || {}

      const chartData = charts.map(item => {
        item.preset = item.value / totalNum
        item.type = item.type*1 === 0 ? 'wrong' : item.type*1 === 1 ? 'partRight' : 'right'
        return item
      })

      if (this.$refs.chartsRef) createPie(this.$refs.chartsRef, chartData)
    },

    async handleTeacherTackUp () {
      this.var_teacherHasTakeUp = true

      window.location.hash = '#hasTakeUp'

      if (!this.var_initReady) return this.requestAllResultPageData()

      await $loading({
        message: '老师正在收卷，请稍后...',
        requestList: [
          this.requestAllResultPageData(),
          sleep(1000)
        ]
      })
    },

    // 关闭结果页
    handleCloseWebPage () {
      window.location.href = 'https://www.baidu.com'
    }
  }
}
</script>
