import axios from 'axios'
import qs from 'querystringify'
import queryParams from 'query-params'

const chs = !!queryParams.chs

console.log(chs)

const createRequest = axios.create({
  baseURL: chs ? 'https://live.chs.xueersi.com' : 'https://live.xueersi.com/science',
  // baseURL: chs ? '/' : '/science',
  transformRequest: [
    data => qs.stringify(data)
  ]
})

const request = (req) => new Promise((resolve, reject) => {
  req
    .then(res => {
      if (res.status === 200) {
        resolve(res.data)
      } else {
        reject(new Error('API 接口请求错误！ Code: ' + res.status))
      }
    })
    .catch(err => reject(err))
})


// 获取作答结果接口
const API_getStudentAnsweredResultData = (params) => request(
  createRequest.get(
    '/LiveExam/getStuTestResult',
    { params }
  )
)

// 组内答题情况饼状图统计接口
const API_getTeamAnwseredResultPieData = (params) => request(
  createRequest.get(
    '/LiveExam/teamTestStatistics',
    { params }
  )
)

// 学生排名情况列表（组内前15名&班内前15名）
const API_getTeamTop15ListData = (params) => request(
  createRequest.get(
    '/LiveExam/testStatisticsList',
    { params }
  )
)

// 是否有连对
const API_ifNeedShowRightAnimation = ({ liveId }) => request(
  createRequest.get('/Stimulation/isOpenCouseWare', { params: { liveId } })
)

// 获取连对数量接口
const API_getStudentRightManyTimeData = (params) => request(
  createRequest.get('/Stimulation/getRightNums', { params })
)

export default {
  API_getStudentAnsweredResultData,
  API_getTeamAnwseredResultPieData,
  API_getTeamTop15ListData,
  API_ifNeedShowRightAnimation,
  API_getStudentRightManyTimeData,
}
