<template>
  <transition name="fade" @after-leave="destroyElement">
    <div class="animation" v-show="visible" ref="aniRef"></div>
  </transition>
</template>

<script>
import lottie from 'lottie-web'

const sleep = d => new Promise(resolve => setTimeout(resolve, d))

export default {
  data () {
    return {
      visible: false,
      animationPath: ''
    }
  },

  methods: {
    destroyElement () {
      this.visible = false
      this.$destroy(true)
      this.$el.parentNode.removeChild(this.$el)
      this.ani.destroy()
    },

    createAnimation () {
      const ani = lottie.loadAnimation({
        container: this.$refs.aniRef,
        path: this.animationPath,
        autoplay: true,
        loop: false,
        renderer: 'svg'
      })
      return ani
    },

    playOnce () {
      const playAnimationPromise = new Promise(async (resolve) => {
        this.visible = true

        await sleep(20)

        this.ani = this.createAnimation()

        this.ani.onComplete = () => {
          this.destroyElement()
          resolve()
        }
      })
      return playAnimationPromise
    }
  }
}
</script>

<style scoped>
.animation {
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 2000;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.6);
}
</style>
